<template>
    <div class="">
        <div class="media">
            <div class="media-left media-top">
                <a href="#">
                <img class="media-object" src="data:image/svg+xml;base64," alt="image">
                </a>
            </div>
            <div class="media-body">
                <blockquote>
                    <h2 class="media-heading">{{ post.title }}</h2>
                    <footer>{{ post.subtitle }}</footer>
                </blockquote>
                {{ post.content }}
                <p class="post-meta">
                    Posted by {{ post.author }}on {{ post.date }}
                </p> 
            </div>
        </div>
    </div>
</template>

<script>

export default {
  name: 'home',
  props: ['post'],
  data () {
    return{
        
    }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
blockquote{
    margin: 0;
}
</style>



